                >{{tran("One activation a day!")}}</b-dropdown-text>

                <template v-else>
                  <b-dropdown-item
                    v-if="deviceId && !isCurrentDeviceActivated"
                    class
                    v-on:click="doActivateDevice(item.license.licenseKey, deviceId)"
                  >{{tran("Activate Current Device")}}</b-dropdown-item>
                  <b-dropdown-item
                    class
                    v-on:click="doActivateDevice(item.license.licenseKey)"
                  >{{tran("Activate Another Device")}}</b-dropdown-item>
