</template>

<script>
import config from "../../config";
import axios from "axios";
import moment from "moment";
import i18n from "../i18n";

function isEmail(text) {
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(text).toLowerCase());
}

function isLicenseKey(text) {
  var re = /^[A-Z0-9]{5}-[A-Z0-9]{5}-[A-Z0-9]{5}-[A-Z0-9]{5}$/;
  return re.test(String(text));
}

function isDeviceSN(text) {
  var re = /^[A-Za-z0-9]{11,12}$/;
  return re.test(String(text));
}

let lang = "en";

export default {
  name: "Manage",
  data() {
    return {
      lang: this.$store.state.language,
      deviceId: this.$store.state.currentDeviceId,

      text: null,
      textType: null,
      code: null,
      showQueryButton: false,
      queried: false,
      isCurrentDeviceActivated: false,
      licenses: [],
      loading: false
    };
  },
  beforeMount() {
    if (this.$route.params.email) {
      this.text = this.$route.params.email;
    } else if (this.deviceId) {
      this.text = this.deviceId;
    }
    this.updateType();
    if (this.text) {
      this.queryLicenses();
    }
  },
  watch: {
    text: function(newVal, oldVal) {
      if (newVal == oldVal) return;
      this.text = newVal;
      this.updateType();
    }
  },
  methods: {
    back() {
      this.$router.push({ name: "home" });
    },
    updateType() {
      if (isEmail(this.text)) {
        this.textType = "email";
        this.showQueryButton = true;
      } else if (isLicenseKey(this.text)) {
        this.textType = "license";
        this.showQueryButton = true;
      } else if (isDeviceSN(this.text)) {
        this.textType = "sn";
        this.showQueryButton = true;
      } else {
        this.textType = null;
      }
    },
    tran(key) {
      return i18n[this.lang][key];
    },
    timeFormat(t) {
      return moment
        .utc(t)
        .local()
        .format("YYYY-MM-DD HH:mm:ss");
    },
    checkCurrentDevice() {
      let self = this;

      if (!self.deviceId) return;

      self.loading = true;

      const url = `${config.server}/license?id=${self.deviceId}`;

      axios
        .get(url)
        .then(function(response) {
          const { validated } = response.data;
          self.isCurrentDeviceActivated = validated;
        })
        .catch(function(error) {
          alert(error.response.data);
        })
        .finally(function() {
          self.queried = true;
          self.loading = false;
        });
    },
    queryLicenses() {
      const self = this;

      if (!self.textType) {
        return;
      }

      let by;
      switch (self.textType) {
        case "email":
          by = "PAYPAL_EMAIL";
          break;
        case "license":
          by = "LICENSE_KEY";
          break;
        case "sn":
          by = "ACTIVATED_DEVICE";
          break;
        default:
          return;
      }

      let value = self.text;
      if (!value) {
        return;
      }
      value = value.trim();
      if (value == "") {
        return;
      }

      self.loading = true;

      const url = `${config.server}/query`;

      axios
        .get(url, {
          params: {
            by,
            value
          }
        })
        .then(function(response) {
          const { items, total } = response.data;
          self.licenses = items;
          self.total = total;
        })
        .catch(function(error) {
          alert(error.response.data);
        })
        .finally(function() {
          self.queried = true;
          self.loading = false;
        });
    },
    doActivateDevice(licenseKey, deviceSign) {
      const self = this;

      if (!licenseKey) {
        licenseKey = prompt(this.tran("License Key you want to use:"), "");
        // If the user clicks "OK", the input value is returned. If the user clicks "cancel", null is returned.
        // If the user clicks OK without entering any text, an empty string is returned.
        if (!licenseKey) {
          return;
        }
      }

      if (!deviceSign) {
        deviceSign = prompt(
          this.tran("Serial Number of the device you want to activate:"),
          ""
        );
        if (!deviceSign) {
          return;
        }
      } else {
        if (
          !confirm("Are you sure to activate current device with this license?")
        ) {
          return;
        }
      }

      self.loading = true;

      const url = `${config.server}/activate`;
      axios
        .get(url, {
          params: {
            licenseKey: licenseKey.trim(),
            deviceSign: deviceSign.trim()
          }
        })
        .then(function() {
          alert("Activation successful!");
          if (self.deviceId) {
            self.checkCurrentDevice();
          }
          self.queryLicenses("ACTIVATED_DEVICE");
        })
        .catch(function(error) {
          alert(error.response.data);
        })
        .finally(() => {
          self.loading = false;
        });
    },
    isExpired: function(t) {
      if (!t) return false;
      const expireTime = moment(t);
      const utcNow = moment()
        .utc()
        .toDate();
      return utcNow > expireTime;
    },
    isActivationLocked(activationLockTo) {
      if (!activationLockTo) return false;
      const activationLockToTime = moment.utc(activationLockTo);
      const utcNow = moment().utc();
      return utcNow < activationLockToTime;
    }
  }
};
</script>

