<template>
  <div class="container" v-cloak>
    <b-overlay :show="loading" rounded="sm" no-wrap z-index="1000">
      <template v-slot:overlay v-if="paidSuccessfully">
        <div class="text-center">
          <i class="icon icon-check"></i>
          <h3>Thank you!</h3>
        </div>
      </template>
    </b-overlay>
    <div class="row">
      <div class="col-12 p-3">
        <button class="btn btn-common btn-back" @click="back">Back</button>
      </div>
    </div>
    <div class="row">
      <div class="col-12 p-3">
        <div
          class="card-plan p-3"
          v-bind:class="{ 'basic': plan === 'basic', 'pro': plan === 'pro' }"
        >
          <h6 class="mb-4">You are purchasing AutoTouch license of plan:</h6>
          <div class="d-flex justify-content-between align-items-baseline">
            <h3 class="title">
              <template v-if="plan === 'basic'">Basic</template>
              <template v-if="plan === 'pro'">Pro</template>
            </h3>
            <h3 class="price">
              <template v-if="plan === 'basic'">$9.99</template>
              <template v-if="plan === 'pro'">$19.99</template>
              <span class="unit">/ Year</span>
            </h3>
          </div>

          <div class="d-flex justify-content-between align-items-center py-3">
            <h5 class="quantity">Quantity:</h5>
            <b-form-spinbutton min="1" max="100" v-model="quantity" inline></b-form-spinbutton>
          </div>

          <div class="d-flex justify-content-start align-items-baseline">
            <div class="font-weight-bold">
              <span>Total:</span>
              <span class="mx-2" style="font-size: 30px;">$ {{totalAmount}}</span>
              <span>USD</span>
            </div>
          </div>
        </div>
        <div class="mt-5">
          <div ref="paypal" class="paypal-button-container"></div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-12 p-3">
        <div class="mt-5 text-center">
          <router-link :to="`/purchase/${plan}/cn`">
            <h6 class="text-secondary">中国大陆用户请从这里购买</h6>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../../config";
import axios from "axios";
import moment from "moment";
import i18n from "../i18n";

export default {
  name: "Purchase",
  data() {
    return {
      lang: this.$store.state.language,
      deviceId: this.$store.state.currentDeviceId,

      plan: null,
      quantity: 1,

      isCurrentDeviceActivated: false,
      loading: false,
      paidSuccessfully: false
    };
  },
  created() {
    this.plan = this.$route.params.plan;
  },
  mounted: function() {
    this.loading = true;
    const script = document.createElement("script");
    script.src = `https://www.paypal.com/sdk/js?client-id=${config.payPalClientId}&currency=USD`;
    script.addEventListener("load", this.setLoaded);
    document.body.appendChild(script);
  },
  computed: {
    price: function() {
      if (this.plan === "basic") {
        return 999;
      }
      if (this.plan === "pro") {
        return 1999;
      }
      return null;
    },
    totalAmount: function() {
      return (this.quantity * this.price) / 100;
    }
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    tran(key) {
      return i18n[this.lang][key];
    },
    setLoaded() {
      this.loading = false;
      this.renderPayPalButton();
    },
    renderPayPalButton() {
      const self = this;
      window.paypal
        .Buttons({
          style: {
            layout: "horizontal",
            color: "gold",
            shape: "rect",
            label: "paypal",
            size: "responsive",
            height: 55,
            tagline: false
          },
          createOrder: function(data, actions) {
            return actions.order.create({
              purchase_units: [
                {
                  description: `AutoTouch iOS 1 year ${self.plan} license ${self.quantity} units`,
                  amount: {
                    currency_code: "USD",
                    value: String(self.totalAmount),
                    breakdown: {
                      item_total: {
                        currency_code: "USD",
                        value: String(self.totalAmount)
                      }
                    }
                  },
                  reference_id:
                    self.quantity === 1 && self.deviceId
                      ? self.deviceId
                      : "no_sn",
                  items: [
                    {
                      name: `AutoTouch iOS 1 year ${self.plan} license`,
                      description: self.plan,
                      quantity: self.quantity,
                      unit_amount: {
                        currency_code: "USD",
                        value: String(self.price / 100)
                      }
                    }
                  ]
                }
              ]
            });
          },
          onApprove: function(data, actions) {
            self.loading = true;

            return actions.order.capture().then(function() {
              axios
                .post(`${config.server}/paypal/transactionComplete`, {
                  orderId: data.orderID
                })
                .then(function(response) {
                  self.paidSuccessfully = true;
                  setTimeout(() => {
                    self.paidSuccessfully = false;
                    self.loading = false;
                    const { email } = response.data;
                    self.$router.push({ name: "manage", params: { email } });
                  }, 3000);
                })
                .catch(function(error) {
                  self.loading = false;
                  alert(error.response.data);
                });
            });
          },
          onError: error => {
            alert(error);
            this.loading = false;
          }
        })
        .render(self.$refs.paypal);
    }
  }
};
</script>

