<template>
  <div class="container" v-cloak>
    <div class="row">
      <div class="col-12 p-3">
        <button class="btn btn-common btn-back" @click="back">Back</button>
      </div>
    </div>
    <div class="row">
      <div class="col-12 p-3">
        <div>
          <h6 class="mb-3">中国大陆用户请通过如下途径购买，{{plan}}版本一年授权价格{{price/100}}美元：</h6>
          <form id="form_pay" name="form_pay" :action="config.paypal.pdt" method="post">
            <div style="display:none">
              <input type="hidden" name="cmd" value="_xclick" />
              <input type="hidden" name="business" value="iain912@hotmail.com" />
              <input
                type="hidden"
                name="item_name"
                :value="`AutoTouch iOS 1 year ${plan} license 1 unit`"
              />
              <input type="hidden" name="item_number" :value="quantity" />
              <input type="hidden" name="amount" :value="totalAmount" />
              <input type="hidden" name="currency_code" value="USD" />
              <input type="hidden" name="return" :value="config.paypal.returnUrl" />
              <input type="hidden" name="notify_url" :value="config.paypal.notifyUrl" />
              <input type="hidden" name="invoice" :value="deviceId" />
              <input type="hidden" name="custom" :value="plan" />
              <input type="hidden" name="plan" :value="plan" />
              <input type="hidden" name="lc" value="EN" />
            </div>
            <input type="submit" value="用PayPal购买" class="btn btn-common btn-cn" />
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from "../../config";

export default {
  name: "PurchaseCn",
  data() {
    return {
      lang: this.$store.state.language,
      deviceId: this.$store.state.currentDeviceId,

      plan: null,
      quantity: 1,
      config: config
    };
  },
  created() {
    this.plan = this.$route.params.plan;
  },
  computed: {
    price: function() {
      if (this.plan === "basic") {
        return 999;
      }
      if (this.plan === "pro") {
        return 1999;
      }
      return null;
    },
    totalAmount: function() {
      return (this.quantity * this.price) / 100;
    }
  },
  methods: {
    back() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped lang="scss">
.btn-cn {
  width: 100%;
  background-color: #fec339;
  height: 55px;
}
</style>